import app from '@/common/app.js'; //公共变量
import util from '@/common/util.js'; //公共变量
import uniSwiperDot from '@/components/uni-swiper-dot/uni-swiper-dot.vue';
import segmentedControl from '@/components/uni-don-segmented-control/uni-don-segmented-control.vue';
import mSearch from '@/components/act-mehaotian-search/mehaotian-search.vue';
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
import request from '@/common/request.js';
import '@/static/css/index.css';
import {
    mapState
} from 'vuex';
export default {
    components: {
        uniSwiperDot,
        mSearch,
        segmentedControl,
        uniLoadMore
    },
    data() {
        return {
            items: ['回馈项目', '共同献爱心'],
            swiperDatas: [],
            donProlist: [],
            teamDonList: [],
            pageNo: 0,
            pageSize: 10,
            cur: 0,
            mode: 'long',
            tab_type: 0,
            status: "more",
            contentText: {
                contentdown: '加载更多',
                contentrefresh: '加载中',
                contentnomore: '没有更多'
            },
            current: 0,
            modeIndex: -1,
            styleIndex: -1,
            dotsStyles: {},
            imagePath: app.imagePath, //图片路径
            moreflag: false, //捐赠是否有上拉更多
            pagenum: 0,
            moretemflag: false, //一起捐是否有上拉更多
            pagetemnum: 0,
            serachvalue: "",
            disabled: false,
            is_don_project: "",
            is_don_team: ""
        }
    },
    computed: {
        ...mapState(['hasLogin','configParams'])
    },
    onReachBottom() {
        if (this.cur === 1 && this.moretemflag) {
            this.status = "loading";
            setTimeout(() => {
                this.getTeamDonList();
            }, 2000, null);
        } else if (this.cur === 0 && this.moreflag) {
            this.status = "loading";
            setTimeout(() => {
                this.getDonProList();
            }, 2000, null);
        } else {
            this.status = "noMore";
        }
    },
    onLoad() {},
    onTabItemTap() {
        //util.getStatPoint('switchTab','EVENT_DESCRIBE_46')   
    },
    onShow() {
        this.is_don_project = this.configParams.is_don_project;
        this.is_don_team = this.configParams.is_don_project;
        if (this.donProlist.length == 0) {
            this.getDonProList();
        }
        if (this.swiperDatas.length == 0) {
            this.getswiperDatas();
        }
    },
    methods: {
        change(e) {
            this.current = e.detail.current
        },
        clear() {
            console.log("清空搜索数据")
        },
        search(e, b) {
            if (this.disabled) {
                return false;
            }
            this.disabled = true;
            this.serachvalue = e;
            this.isSearch = true;
            //将状态修改为搜索，并清空捐赠项目和一起捐的搜索列表
            this.donProlist = [];
            this.teamDonList = [];
            this.pagenum = 0;
            this.pagetemnum = 0;
            if (this.cur === 0) {
                this.status = "loading";
                this.getDonProList();
            } else if (this.cur === 1) {
                this.status = "loading";
                this.getTeamDonList();
            }
        },
        selectStyle(index) {
            this.dotsStyles = this.dotStyle[index]
            this.styleIndex = index
        },
        selectMode(mode, index) {
            this.mode = mode
            this.modeIndex = index
            this.styleIndex = -1
            this.dotsStyles = this.dotStyle[0]
        },
        onClickItem(index) {
            this.donProlist = [];
            this.teamDonList = [];


            this.serachvalue = '';
            this.$refs.mSearch.clear();
            // this.clear();
            if (this.cur !== index) {
                this.cur = index;
            }
            if (this.cur == 0) {
                this.pagenum = 0;
                //util.getStatPoint('click','EVENT_DESCRIBE_19');
                this.getDonProList();
            } else if (this.cur == 1) {
                this.pagetemnum = 0;
                //util.getStatPoint('click','EVENT_DESCRIBE_21');
                this.getTeamDonList();

            }
        },
        getDonProList() {
            this.pagenum = this.pagenum + 1;
            request.http({
                // url: app.serverPath + "/don/teamDonList.json",
                url: "/app/don/donProList",
                data: {
                    pageNo: this.pagenum,
                    pageSize: this.pageSize,
                    name: this.serachvalue
                },
                header: 'application/x-www-form-urlencoded',
                success: (res) => {
                    //  let list = this.setTime(res.data.data);
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        var datas = res.data.list;
                        if (datas.length === 0) {
                            this.status = "noMore";
                            return;
                        }
                        datas.forEach((d) => {
                            var percent = 0;
                            if (d.gotMoney < d.targetMoney) {
                                percent = Number(d.gotMoney / d.targetMoney * 100).toFixed(0)
                            }
                            d.name = util.strcharacterDiscode(d.name);
                            d.summary= util.strcharacterDiscode(d.summary);
                            d["percent"] = percent;
                            this.donProlist.push(d);
                        });
                    }
                    this.moreflag = (res.data.pages > (this.pagenum) ? true : false);
                    if (!this.moreflag) {
                        this.status = "noMore";
                    }
                    this.disabled = false;
                },
                fail: (data, code) => {
                    console.log(data);
                }
            })
        },
        getswiperDatas() {
            request.http({
                // url: app.serverPath + "/don/donProList.json",
                url: "/app/don/donProList",
                data: {
                    pageNo: 1,
                    pageSize: 3,
                },
                header: 'application/x-www-form-urlencoded',
                success: (res) => {
                    //  let list = this.setTime(res.data.data);
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        var datas = res.data.list;
                        datas.forEach((d) => {
                            d.name = util.strcharacterDiscode(d.name);
                        });
                        this.swiperDatas = datas;
                    }
                },
                fail: (data, code) => {
                    console.log(data);
                }
            })
        },
        tabClick(e) {
            this.tab_type = e;
        },
        getTeamDonList() {
            this.pagetemnum = this.pagetemnum + 1;
            request.http({
                url: "/app/don/teamDonRecords",
                data: {
                    pageNo: this.pagetemnum,
                    pageSize: this.pageSize,
                    name: this.serachvalue
                },
                header: 'application/x-www-form-urlencoded',
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        // this.teamDonList = res.data.data;
                        var datas = res.data.list;
                        console.log(this.teamDonList);
                        if (datas.length === 0) {
                            this.status = "noMore";
                            return;
                        }
                        datas.forEach((d) => {
                            this.teamDonList.push(d);
                        });

                    }
                    this.moretemflag = (res.data.pages > (this.pagetemnum) ? true : false);
                    console.log("一起捐状态：" + this.moretemflag)
                    if (!this.moretemflag) {
                        this.status = "noMore";
                    }
                    this.disabled = false;
                },
                fail: (data, code) => {
                    console.log(data);
                }
            })
        },
        goDetail(detail) {
            var url = '/pages/don/donDetail?id=' + encodeURIComponent(detail);
            if (this.cur == 1) {
                // url = '/pages/don/teamDonDetail?id=' + encodeURIComponent(JSON.stringify(detail));
                url = '/pages/don/ImmediateDon?id=' + encodeURIComponent(JSON.stringify(detail));
            }

            uni.navigateTo({
                url
            });
        },
        goBannerDetail(detail) {
            //util.getStatPoint('click','EVENT_DESCRIBE_20');
            var url = '/pages/don/donDetail?id=' + encodeURIComponent(detail);
            uni.navigateTo({
                url
            });
        },
        goDon() {
            var url = '/pages/don/donPay';
            uni.navigateTo({
                url
            });
        }
    }
}
